<template>
    <component class="LOADER-ROOT" :is="targetComponent" :data="currentData" ></component>
</template>
<script>
/**
 * DYANAMIC COMPONENT LOADER WHICH CAN LOAD COMPONENT BASED ON THE DATA BEING FED
 *  string:
 *          URL=>MODULE-LOADER
 *          STRING:component-name=>COMPONENT
 *          OBJECT:OBJECT.is =>COMPONENT
 *          PROMISE:resolves  promise and use the returned object to load component
 */
import v from '../validators.js';
var {vURL,vNumber,vString,vPromise,vObject,vRequired,$OR,$AND}=v;

export default {
    name:"DYNAMIC-LOADER",
    components:{
        cube3dView:()=>import('./i.v.s.box.3d.cube'),
        formView:()=>import('./form'),

        listView:()=>import('./v.s.list.vue'),
        inputView:()=>import('./i.v.s.box.formelement.input.vue'),
        fluidrightView:()=>import('./v.s.list.fluidRight.vue'),
        fluiddownView:()=>import('./v.s.list.fluidDown.vue'),
        burgerView:()=>import('./v.s.list.burger.vue'),
        listitemView:()=>import('./v.s.box.menuItem.vue'),
        windowView:()=>import('./v.s.box.window.vue'),
        navBarView:()=>import('./i.v.s.navbar.vue'),
        snapView:()=>import('./v.s.page.card.snapped'),
        videoView:()=>import('./v.s.video'),
        nullView:()=>import('./v.s.null.vue'),
        imageView:()=>import('./v.s.image.vue'),
        textView:()=>import("./v.s.text.vue"),

        textverticalView:()=>import('./v.s.text.stand.vertical'),
        textBoxView:()=>import("./v.s.text.nameCard-textBox.vue"),
        textStandardView:()=>import("./v.s.text.stand.vue"),
        boxView:()=>import("./i.v.s.box.vue"),
        boxcenterView:()=>import("./i.v.s.box.center.vue"),
        pageView:()=>import("./v.s.page.vue"),
        screenView:()=>import("./v.s.page.screen.vue"),
        cardView:()=>import("./v.s.page.ebulcard.vue"),
        loadErrorView:()=>import("./v.s.text.error.LoadError.vue"),
        // ebullandView:()=>import("../../ebul/launch/ebul-land.vue")
    },
    props:["data"],
    fields:{
        data:$OR(vURL,vPromise,vObject)
    },
    data:function()
    {
        return{
            parsedData:null
        }
    },
    computed:{
        currentData()
        {
            return this.parsedData||this.data;
        },
        targetComponent:function()  
        {
            if(!this.currentData){
                    debugger;
                    return 'null-view';
                }
            else if(vURL(this.currentData))return 'module-loader'
            else if(vPromise(this.currentData))
            {
                this.currentData.then((data)=>
                {
                    this.parsedData=data;
                })
                return 'loader-view'
            }
            else if(vString(this.currentData.is))
            {
                let name=this.currentData.is.split('?')[0]   ;
                
                if(name+'View' in this.$options.components)
                    return name+'-View'
            }
            
            return 'load-error-view'
        }
    },
}
</script>


