const SymbolBubble=Symbol.for('bubblified-event')
function bubblify(data) {
        
        if(data&&data[SymbolBubble])return data;
       var stopped=false;
       return {
        [SymbolBubble]:true,
        get isStopped(){return stopped},
        stopPropagation()
        {
            stopped=true;
        },
        data:data
         }
   }
const bubbly = {
   
    install(Vue, options) {
            Vue.mixin({
                data:()=>{
                    return {
                        $whenever:{}
                    }
                }
            })
            let modifiers={
                stop:(data)=>data.stopPropagation()
            }
            Vue.directive('when', 
            {
                //FIXME: COMPLETE THE FUNCTIONALITY OF MODIFIERS
                inserted:function(el,bind,vn)
                        {
                                let arg=bind.arg;
                                let modifier=Object.keys(bind.modifiers)[0];
                                if(modifier&&!(modifier in modifiers))throw "illegal modifier"
                                if(!vn.componentInstance.$data.$whenever[arg])vn.componentInstance.$data.$whenever[arg]=new Set;
                                vn.componentInstance.$data.$whenever[arg].add((...arg)=>{
                                    bind.value(...arg);
                                    modifiers[modifier](arg[0]);    
                                        }
                                    )
                        }
            })
            Vue.prototype.$say=function(eventName,payload)
            {
                let data=bubblify(payload);
                if(!(typeof eventName== 'string'))throw "illegal event name";
                if(data.isStopped)return;
                if(this.$data.$whenever[eventName])
                {
                    [...this.$data.$whenever[eventName]]
                    .forEach(f=>
                        {
                            if(data.isStopped)return;
                            f.call(this,data);
                        })
                }
                if(this.$parent==this.$root)
                {
                    data.stopPropagation();
                    return;
                }
                Promise.resolve().then(_=>this.$parent.$say(eventName,data));
            }
            // Vue.prototype.$bubble=function(eventName,payload)
            // {
            //     var data=bubblify(payload);
            //     if(!(typeof eventName== 'string'))throw "illegal event name";
            //     if(data.isStopped)return;
            //     this.$parent.$emit(eventName,data);
            //     if(this.$parent==this.$root)
            //     {
            //         data.stopPropagation();
            //         return;
            //     }
            //     Promise.resolve().then(_=>this.$parent.$bubble(eventName,data))
            // }
      }
}

  export default bubbly;
  


