

function $One(data,type)
{
    if(!(type in v))
    {
        if(typeof type=='string')
        {
            return data==type
        }
        else if(type instanceof RegExp)
        {
            return type.test(data);
        }
        else if(typeof type=='function')
        {
            if(!(data instanceof type))
            {
                return !!type(data);
            }
            return true;
        }
        else return !!ValidateMap(data,type);
    }
    else return v[type](data);
}

function $ANY(data,...types)
{
    for(let each of types)
    {
        if(v.$One(data,each))return true
    }
    return false;
}



function $ALL(data,...types)
{   
    for(let each of types)
    {
        if(!v.$One(data,each))return false
    }
    return true;
}




function $ValidateMap(obj,map)
{
    if(typeof map=='function')
    {
        return !!map(obj);
    }
    else if(typeof obj!='object') throw "illegal obj use normal method instaed";
    else for(let each in map)
    { 
      let val1=obj[each];
      let val2=map[each];
      if(typeof val1=='object'){
        if(!v.$ValidateMap(val1,map[each]))
          return  false;
      }
      else if(!v.$ALL(val1,val2))return false;
    }
    return true
}
function $OR(...types)
{
    return (data)=>v.$ANY(data,...types);
}
function $AND(...types)
{
    return (data)=>v.$ALL(data,...types);
}
function $MAP(map)
{
    return (data)=>v.$ValidateMap(data,map)
}


function vURL(data)
{
    if(!v.vString(data))return false
        // let r=/^(https?:\/\/)?((([a-z\d]([a-z\d-]*[a-z\d])*)\.)+[a-z]{2,}|((\d{1,3}\.){3}\d{1,3}))(\:\d+)?(\/[-a-z\d%_.~@/+]*)*(\?[;&a-z\d%_.~@/+=-]*)?(\#[-a-z\d_]*)?$/i
        let r=/^(https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|www\.[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9]+\.[^\s]{2,}|www\.[a-zA-Z0-9]+\.[^\s]{2,}|[a-zA-Z0-9]+\.[^\s]{2,})$/
        // console.log('=>6',r.test(data))
        return r.test(data)
}




function vPromise(data)
{
    if(!v.vExist(data))return false
    else if(!v.vFunction(data.then))return false
    else 
    {
        return true
    }
}




function vVueComponent(data)
{
    if(!v.$ALL(data,'vExist'))return false;
    if(data.constructor.name="VueComponent")return true
    else return false
}

let v={
    vOptional:(data)=>true,
    vRequired:(data)=>v.Exist(data),
    vFunction:(data)=> typeof data=='function'||data instanceof Function,
    vObject:(data)=>v.vExist(data)&&typeof data=='object'&&!v.vArray(data),
    vArray:(data)=>data instanceof Array,
    vUndefined:(data)=>data===undefined,
    vString:(data)=>typeof data=='string'||data instanceof String,
    vNumber:(data)=>typeof data=='number'||data instanceof Number,
    vNull:(data)=>data===null,
    vExist:(data)=>!v.$ANY(data,'vUndefined','vNull'),
    vBoolean:(data)=>typeof data=='boolean',
    vURL,
    vPromise,
    vVueComponent,
 
    $ALL,
    $ANY,
    $One,
    $OR,
    $AND,
    $MAP,
    $ValidateMap
}

var handler={
    get(target,prop)
    {
        if(!target[prop])throw "not awailable now"
        return target[prop];
    }
}


export default new Proxy(v,handler)


