import Vars from './validators'
var  SymbolFieldingMap=Symbol.for('fielding')
var  SymbolOldMap=Symbol.for('oldy');

function SetAutoClassTag(com)
{
    if(com&&com.$el&&com.$el.classList&&typeof  com.$el.classList.add=='function')
    com.$el.classList.add(com.$options.name);
    if(!com.$el.$autsetClasses)
        com.$el.$autsetClasses=[];
    com.$el.$autsetClasses.push(com.$options.name);
}
function unSetAutoClassTag($el)
{
    if(com&&com.$el&&com.$el.classList&&typeof  $el.classList.remove=='function')
    {   
         if(!com.$el.$autsetClasses)
        {
            com.$el.$autsetClasses.forEach(item=>com.$el.classList.remove(item));
            com.$el.$autsetClasses=[];
        }
        else 
        com.$el.classList.remove(com.$options.name);
    }
}
function generateParam($props)
{
    if($props&&$props.data&&typeof $props.data.is=='string')
    {
        if($props.data.is.search('/?')>-1)
        {
            return [...new URLSearchParams($props.data.is.split('?')[1])]
                .reduce((params,item)=>
                (params[item[0]]=item[1],params)
                    , {} )
        }
    }
}
var plug={
    install(Vue)
    {
        
        
        Vue.mixin({
            fields:{},
            [SymbolFieldingMap]:null,
            [SymbolOldMap]:null,
            beforeCreated()  {
             if(Object.keys(this.$options.fields).length)this[SymbolFieldingMap]=true;
            },
            mounted() {
                SetAutoClassTag(this);
                // this.$options.params=generateParam(this.$options.propsData);
                if(!this[SymbolFieldingMap])return 
               if(!Vars.$ValidateMap(this.$options.propsData,this.$options.fields))
               {
                   console.error(this.$options.propsData,this.$options.fields)
                throw `match failed in ${7}`;
               }
            },
            unmounted()
            {
                unSetAutoClassTag(this);
            },
            computed:{
                params()
                {
                    let $props=this.$options;
                    if($props&&$props.propsData&&$props.propsData.data&&typeof $props.propsData.data.is=='string')
                     {
                        if( $props.propsData.data.is.search('/?')>-1)
                        {
                            return [...new URLSearchParams( $props.propsData.data.is.split('?')[1])]
                                .reduce((params,item)=>
                                (params[item[0]]=item[1],params)
                                    , {} )
                        }
                    }
                    return {};
                },
                styles()
                {
                    let styles={};
                    for(let each in this.params)
                    {
                        if(each.startsWith('--'))
                            styles[each]=this.params[each]
                    }
                    return styles;
                }
            }
        })
    }
}
export default plug


